import React from 'react';
import get from 'lodash/get';
import { useFormik } from 'formik';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import WarehouseIcon from '@mui/icons-material/Warehouse';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import AddPhotoAlternate from '@mui/icons-material/AddPhotoAlternate';

import TimeFrameForm from './timeFrame';
import { SearchField, TextField } from '../components';

import { FacilitiesQuery } from '../api';
import { Facility, Freight, TimeFrameType } from '../utils/dataTypes';
import FreightForm from './freight';
import FreightListForm from './freightList';

export default function StopPickUpForm({
  fieldName,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const [expanded, setExpanded] = React.useState<boolean>(false);

  const handleExpandedChange = () => {
    setExpanded((prev) => !prev);
  };

  return (
    <Accordion
      expanded={expanded}
      onChange={handleExpandedChange}
      sx={{ backgroundColor: 'unset' }}
    >
      <AccordionSummary
        expandIcon={<ArrowDropDownIcon />}
        aria-controls="stop-pickup-content"
        id="stop-pickup-header"
      >
        <Box
          sx={{
            width: '100%',
            display: 'flex',
            justifyContent: 'space-between',
          }}
        >
          <Typography>Stop PickUp</Typography>
          <IconButton
            component="label"
            aria-label="add"
            sx={{ padding: 0 }}
            disabled={disabled}
          >
            <AddPhotoAlternate />
          </IconButton>
        </Box>
      </AccordionSummary>
      <AccordionDetails>
        <SearchField<Facility>
          fieldName={`${fieldName}.facility`}
          label="Facility"
          getSelectedLabel={(selectedItem) => `${selectedItem.name}`}
          getItemLabel={(item) => `${item.name}`}
          getItemSecondLabel={(item) => `${item.address}`}
          ListIcon={WarehouseIcon}
          queryFn={FacilitiesQuery}
          startEmpty={false}
          orderby="name"
          order="asc"
          disabled={disabled}
          formik={formik}
        />
        <TimeFrameForm
          fieldName={`${fieldName}.timeFrame`}
          values={[TimeFrameType.FCFS, TimeFrameType.APPT, TimeFrameType.ASAP]}
          disabled={disabled}
          formik={formik}
        />
        <FreightListForm
          fieldName={`${fieldName}.freightList`}
          disabled={disabled}
          formik={formik}
        />
        <TextField
          fieldName={`${fieldName}.addInfo`}
          label="Additional stop info"
          disabled={disabled}
          formik={formik}
        />
      </AccordionDetails>
    </Accordion>
  );
}
