import React from 'react';
import get from 'lodash/get';
import { useFormik } from 'formik/dist/Formik';
import Box from '@mui/material/Box';

import StopPickUpForm from './stopPickUp';
import StopDeliveryForm from './stopDelivery';
import { StopDelivery, StopPickUp, StopType } from '../utils/dataTypes';

export default function StopsForm({
  fieldName,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  // stops: (StopPickUp | StopDelivery)[];
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const stops = get(formik.values, fieldName) as (StopPickUp | StopDelivery)[];

  return (
    <Box key="stops" sx={{ paddingBottom: 3, width: '100%' }}>
      {!stops
        ? null
        : stops.map((stop, index) => {
            switch (stop.type) {
              case StopType.PickUp:
                return (
                  <StopPickUpForm
                    key={index}
                    fieldName={`${fieldName}[${index}]`}
                    disabled={disabled}
                    formik={formik}
                  />
                );
              case StopType.Delivery:
                return (
                  <StopDeliveryForm
                    key={index}
                    fieldName={`${fieldName}[${index}]`}
                    disabled={disabled}
                    formik={formik}
                  />
                );
            }
          })}
    </Box>
  );
}
